.project-galery {
  background-color: $light-gray;

  height: 0;
  overflow: hidden;

  &__container {
    padding: 3.75rem 0;

    @include tablet {
      padding: 5rem 0;
    }

    @include desktop {
      padding: 6.25rem 0;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2.5rem;

    @include tablet {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__body {
    padding-top: 3.125rem;
    text-align: center;
  }
}

