.info {
  position: relative;

  &__title {
    font-size: 1.5rem;
    line-height: 1.2;
    color: $black;

    @include desktop {
      font-size: 1.875rem;

    }
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr;
    grid-column-gap: 7vw;
    grid-row-gap: 2.5rem;
    margin-top: 4rem;

    @include desktop {
      grid-template-columns: 1fr 0.6fr;
      margin-left: 7vw;
    }
  }

  &__text {
    font-size: 1rem;
    line-height: 1.625rem;
    color: $black;

    @include desktop {
      font-size: 1.25rem;
      line-height: 1.5;
    }

    &:not(:last-of-type) {
      margin-bottom: 0.5rem;

      @include tablet {
        margin-bottom: 2rem;
      }
    }
  }

  &__opinion {
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1.2;
    color: $black;

    @include desktop {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  &__author {
    padding-top: 1.875rem;
  }

  &__name {
    margin-bottom: 0.3125rem;
    font-size: 1.125rem;
    font-weight: 700;
    color: $basic-green;
    line-height: 1;
  }

  &__position {
    font-size: 1.125rem;
    font-weight: 700;
    color: $black;
  }

  &__shape {
    position: absolute;
    top: 320px;
    right: 64px;
    z-index: -1;
    opacity: 0.7;
  }
}
