.volunteers {

  &__item {
    grid-column: span 4;

    @include tablet {
      grid-column: span 6;
    }

    @include desktop {
      grid-column: span 3;
    }

    &.anim-items {
      opacity: 0;
      transform: translateY(40px);
      transition: transform 1s cubic-bezier(.25,.46,.45,.94), opacity 0.8s cubic-bezier(.25,.46,.45,.94);
    }

    &.animate {
      $basic-delay: 0.32s;
      animation: none;

      @for $i from 1 through 12 {
        &:nth-of-type(#{$i}) {
          transition-delay: $basic-delay;
        }
        $basic-delay: $basic-delay + 0.16s;
      }

      opacity: 1;
      transform: translateY(0);
    }

    // &.animate {
    //   animation: fadeInTop cubic-bezier(.25,.46,.45,.94) forwards 1s;

    //   $basic-delay: 0.32s;

    //   @for $i from 1 through 4 {
    //     &:nth-of-type(#{$i}) {
    //       animation-delay: $basic-delay;
    //     }
    //     $basic-delay: $basic-delay + 0.16s;
    //   }
    // }
  }

  &__group {
    @include desktop {
      grid-column-gap: 0;
    }
  }

  &__info {
    padding: 1rem 0 0 1rem;

    @include tablet {
      padding: 1.5625rem 0 0 1.875rem;
    }
  }

  &__image {
    width: 100%;
    object-fit: cover;
  }

  &__name {
    margin-bottom: 1rem;
  }

  &__link {
    display: flex;
    align-items: center;

    &:hover {
      color: $black;
    }
  }

  &__position {
    padding-left: 1rem;
  }
}
