.page-title {
  font-size: 1.875rem;
  line-height: 2.5rem;
  font-weight: 700;
  color: $black;
}

.page-title--small {
  font-size: 1.25rem;
  line-height: 1.5625rem;
  font-weight: 700;
  color: $black;

  @include desktop {
    font-size: 1.5rem;
    line-height: 1.875rem;
  }
}

.subtitle {
  font-size: 0.875rem;
  line-height: 1.5;
  letter-spacing: 1px;
  font-weight: 500;
  // color: $black;
  text-transform: uppercase;
}

.page-quote {
  &__text {
    display: inline;

    font-size: 1.5rem;
    line-height: 2.875rem;
    font-weight: 700;

    box-shadow: 0 8px 0 #cde4db;
  }
  &__info {
    padding: 1.4rem 0;
    font-size: 1rem;
    line-height: 1;
    color: $black;
  }
}

.text--selected {
  background-color: #cde4db;
  border-bottom: 1px solid $basic-green;
}


