/* Animation */



@keyframes fadeIn {
  0% {
      opacity:0;
  }
  100% {
      opacity:1;
  }
}
@-webkit-keyframes fadeIn {
 0% {
     opacity:0;
 }
 100% {
     opacity:1;
 }
}
@keyframes fadeInTop {
  0% {
      opacity:0;
      transform:translateY(100%);
  }
  100% {
      opacity:1;
      transform:translateY(0);
  }
}
@-webkit-keyframes fadeInTop {
  0% {
      opacity:0;
      transform:translateY(100%);
  }
  100% {
      opacity:1;
      transform:translateY(0);
  }
}


@keyframes cutTextUp {
  from {
      clip-path: inset(0 0 -10% 0);
  }
  to {
      clip-path: inset(0 0 100% 0);
  }
}

@keyframes cutTextDown {
  from {
      clip-path: inset(100% 0 0 0);
  }
  to {
      clip-path: inset(-10% 0 -20% 0);
      opacity: 1;
  }
}

@keyframes imageSlideTop {
  from {
    clip-path: inset(0 0 100% 0);
    animation-timing-function: ease-in-out;
  }
  to {
    clip-path: inset(0 0 -10% 0);
  }
}

@keyframes slideTop {
  from {
      transform: translateY(0);
  }
  to {
      transform: translateY(-100%);
  }
}

@keyframes slideBotom {
  from {
      transform: translateY(-100%);
  }
  to {
      transform: translateY(0);
  }
}

@keyframes slideleft {
from {
    transform: translateX(0);
}
to {
    transform: translateX(-100%);
    opacity: 0;
}
}

@keyframes slideRight {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}


/* Animation classes */

.anim-items {
  @include tablet {
    opacity: 0;
  }
}

.animate {
  @include tablet {
    animation: fadeInTop cubic-bezier(.25,.46,.45,.94) forwards 1s;
    // animation-delay: 0.4s;
  }
}

@mixin animObject($distance, $delay, $duration) {
  opacity: 0;
  transition-property: opacity, transform;
  transition-timing-function: cubic-bezier(.19,1,.22,1);
  transform: translate3d(0px, $distance + px, 0px);
  transition-delay: $delay;
  transition-duration: $duration;
}

@mixin animateObject {
  opacity: 1;
  transform: none;
  animation: none;
}

