.goals {
  $goals-gray: #F1F1F1;

  background-color: $goals-gray;

  &__title {
    font-size: 2rem;
    line-height: 1.2;
    font-weight: 700;
    color: $black;
    margin-bottom: 3.75rem;

    @include desktop {
      font-size: 3.0625rem;
      line-height: 3.5rem;
    }
  }

  &__desc {
    font-size: 1.125rem;
    line-height: 1.5;
    font-weight: 500;
    color: $black;
    margin-bottom: 2.5rem;

    @include desktop {
      font-size: 1.25rem;
    }
  }

  &__list {
    list-style: square;
    padding-left: 1.125rem;
  }

  &__list-item {
    font-size: 1.125rem;
    line-height: 1.5;
    color: rgba($darkgrey, 0.8);
    margin-bottom: 0.625rem;

    &::marker {
      color: $basic-green;
    }
  }
}

.goals-2 {
  background-image: linear-gradient(180deg,hsla(0,0%,100%,.4),hsla(0,0%,100%,.4));
}

.goals-3 {
  background-image: linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,.8));
}

