.carousel {
  position: relative;
  text-align: center;

  &__container {
    position: relative;
    width: 100%;
    overflow: hidden;
  }

  &__item {
    position: relative;
    display: none;
    animation: 0.5s ease-in-out;

    img {
      width: 100%;
    }
  }

  &__item--active, &__item--next {
    display: block;
  }

  &__item--next {
    position: absolute;
    top: 0;
    width: 100%;
  }

  &__item--to-left {
    animation-name: left;
  }

  &__item--to-right {
    animation-name: right;
    animation-direction: reverse;
  }

  &__item--from-left {
    animation-name: left;
    animation-direction: reverse;
  }

  &__item--from-right {
    animation-name: right;
  }

  &__control {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    bottom: 0;
    left: 0;
    width: 15%;
    cursor: pointer;
  }

  &__control--right {
    right: 0;
    left: auto;
  }

  &__indicators {
    position: absolute;
    bottom: 1.25rem;
    left: 50%;
    margin: 0.1rem;
    padding-left: 0;
    list-style: none;
    transform: translateX(-50%);
  }

  &__indicator {
    display: inline-block;
    margin: 0.1rem;
    width: 0.625rem;
    height: 0.625rem;
    border: 1px solid $white;
    border-radius: 0.625rem;
    cursor: pointer;
  }

  &__indicator--active {
    background-color: $white;
  }
}

@keyframes left {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}

@keyframes right {
  from {
    left: 100%;
  }
  to {
    left: 0;
  }
}
