*,
*::before,
*::after {
  box-sizing: border-box; }

* {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-display: swap;
  color: #4D5250;
  background-color: #FFFFFF;
  overflow-x: hidden;
  opacity: 0;
  animation: fadeIn cubic-bezier(0.785, 0.135, 0.15, 0.86) forwards 0.7s;
  animation-delay: .3s; }

a {
  text-decoration: none;
  color: #4D5250;
  cursor: pointer; }
  a:hover {
    color: #2BB673; }
  a:active {
    color: #2BB673;
    opacity: 0.75; }

h1, h2, h3 {
  font-family: "Gilroy", sans-serif; }

p {
  font-size: 1rem;
  line-height: 1.625rem;
  color: #1D1D1D; }

.text-uppercase {
  text-transform: uppercase; }

img {
  max-width: 100%;
  height: auto;
  aspect-ratio: attr(width)/attr(height); }

.list-style-none {
  list-style: none; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  overflow: hidden; }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(29, 29, 29, 0.4);
  opacity: 0;
  display: none;
  z-index: 4; }

/* Animation */
@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeInTop {
  0% {
    opacity: 0;
    transform: translateY(100%); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@-webkit-keyframes fadeInTop {
  0% {
    opacity: 0;
    transform: translateY(100%); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes cutTextUp {
  from {
    clip-path: inset(0 0 -10% 0); }
  to {
    clip-path: inset(0 0 100% 0); } }

@keyframes cutTextDown {
  from {
    clip-path: inset(100% 0 0 0); }
  to {
    clip-path: inset(-10% 0 -20% 0);
    opacity: 1; } }

@keyframes imageSlideTop {
  from {
    clip-path: inset(0 0 100% 0);
    animation-timing-function: ease-in-out; }
