.last-news {
  background: $background-gray;
  padding: 2.5rem 0;

  &__container {
    padding: 2.5rem 1.25rem 3.75rem;
    background: $white;

    @include tablet {
      padding: 3.75rem 2.5rem 5rem;
    }

    @include desktop {
      padding: 5.625rem 8.375rem 7.5rem;
    }
  }

  &__image {
    margin-bottom: 2.5rem;

    &.anim-items {
      opacity: 1;
      clip-path: inset(0 0 100% 0);
      transform: translateY(0);
      transition: all 0.8s linear;
    }

    &.animate {
      animation: imageSlideTop 0.8s linear forwards;
    }

    @include tablet {
      margin: 0;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__title {
    font-size: 2rem;
    line-height: 1.2;
    font-weight: 700;
    color: #1D1D1D;
    margin-bottom: 1.25rem;

    @include desktop {
      font-size: 3.0625rem;
      line-height: 3.5rem;
    }
  }

  &__text {
    font-size: 1.125rem;
    line-height: 1.5;
    font-weight: 500;
    color: #1D1D1D;
    margin-bottom: 1rem;

    @include tablet {
      font-size: 1.25rem;
      margin-bottom: 3.4375rem;
    }
  }
}
