.common-link {
  display: inline-block;
  text-decoration: underline;
}

.arrow-link {
  display: inline-block;
  transition: all .2s ease-in-out;

  &__icon {
    width: 3.25rem;
    height: 1.5rem;
  }

  &:hover {
    padding-left: 1rem;
  }

  &--no-hover {
    &:hover {
      padding-left: 0;
    }
  }
}
