.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__list {
    display: none;
    justify-content: space-between;

    @include desktop {
      display: flex;
    }
  }

  &__list-item {
    padding: 0 1.41rem;

    &--active {
      .main-nav__link {
        color: $basic-green;
      }
    }

    @include desktop {
      padding: 0 1.5625rem;
    }
  }

  &__link {

    font-size: 1rem;
    font-weight: 400;
    color: $black;
    position: relative;

    &:hover {
      &::after {
        bottom: -5px;
        opacity: 1;
      }
    }

    &::after {
      @include pseudo-element();

      bottom: 20%;

      width:100%;
      height: 2px;
      background-color: $basic-green;

      opacity: 0;
      transition: all 0.3s ease;
    }

    @include desktop {
      font-size: 1.125rem;
    }
  }
}
