.annual {
  padding: 2rem 0 0;

  @include tablet {
    padding: 3.75rem 0 0;
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: $light-blue;
    padding: 2.5rem 0 3.75rem;

    @include tablet {
      padding: 3.75rem 2.5rem 5rem;
    }

    @include desktop {
      padding: 5.625rem 8.375rem 7.5rem;
    }
  }

  &__reports {
    display: flex;
    text-align: center;
    flex-direction: column;

    @include tablet {
      flex-direction: row;
    }
  }

  &__item {
    text-align: center;
    padding: 0 2.5rem;
    margin-bottom: 1rem;

    @include tablet {
      margin: 0;
    }
  }

  &__date {
    margin-bottom: 1.5rem;

    @include tablet {
      margin-bottom: 3.75rem;
    }
  }

  &__link {
    display: block;
    width: 100%;
    padding: 1.375rem;

    font-size: 1rem;
    line-height: 1;
    font-weight: 400;
    color: #1D1D1D;

    border: 1px solid $black;
    transition: all .5s ease;

    &:hover {
      color: $white;
      background-color: $black;
    }
  }
}
