.site-menu {
  position: absolute;
  right: 0;
  top: 0;

  width: 100%;
  padding: 3.75rem 0 1.875rem;

  background-color: $white;
  z-index: 5;

  clip-path: inset(0 0 100% 0);
  transition: all 0.2s;

  @include desktop {
    padding: 5.625rem 0 3.125rem;
  }

  @include bgdesktop {
    padding: 6.4375rem 0 3.125rem;
  }

  &__wrapper {
    position: relative;
    margin: auto;
    padding-left: 20px;
    padding-right: 20px;

    @include tablet {
      padding-left: 30px;
      padding-right: 30px;
    }

    @include desktop {
      padding: 0 1rem;
      max-width: 1624px;
    }

    @include bgdesktop {
      max-width: 1680px;
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    padding-top: 1.875rem;
    border-top: 1px solid $background-gray;

    @include tablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr) 2fr;
      grid-column-gap: 4rem;
    }

    @include desktop {
      padding-top: 3.125rem;
    }

    @include bgdesktop {
      grid-template-columns: 17.5rem 1fr 46.25rem;
      grid-column-gap: 6rem;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
  }

  &__link {
    display: block;
    margin-bottom: 1.25rem;
    width: 100%;

    font-size: 1.125rem;
    font-weight: 300;
    color: $black;
    text-align: center;

    &--main {
      font-weight: 500;
    }

    @include tablet {
      font-size: 1.5rem;
      text-align: left;
    }
  }

  &__news {
    display: none;

    @include tablet {
      display: block;
      padding: 1.875rem 0;

      &:first-of-type {
        border-bottom: 1px solid $background-gray;
        padding-top: 0;
      }
    }
  }

  &__subtitle {
    color: $basic-green;
    margin-bottom: 0.375rem;
  }

  &__href {
    color: $black;

    transition: box-shadow 0.3s ease-in-out;
  }

  &__banner {
    display: flex;
    flex-direction: column;

    padding: 3.125rem 2.1875rem;
    background: $light-blue url(/img/mini-banner.png) no-repeat;
    background-size: cover;
  }

  &__title {
    max-width: 21.25rem;
    margin-bottom: 1.875rem;
  }

  &__group {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    @include tablet {
      flex-direction: row;
      max-width: 19rem;
    }
  }
}

.site-menu--open {
  clip-path: inset(0 0 -10% 0);
}

