.project-detail {
  position: relative;

  &__container {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2rem;

    @include desktop {
      grid-template-columns: 25rem 1fr;
      grid-column-gap: 6vw;
    }
  }

  &__sticky {
    @include desktop {
      position: sticky;
      top: 3rem;
    }
  }

  &__body {
    display: flex;
  }

  &__photo {
    width: 6.875rem;
    height: 6.875rem;
    margin-right: 2.1875rem;
  }

  &__group {
    &:last-of-type {
      padding-top: 2rem;
    }
  }

  &__subtitle {
    margin-bottom: 0.5rem;
    font-weight: 500;
    color: $basic-green;
  }

  &__title {
    margin-bottom: 1.25rem;
  }

  &__image {
    margin: 3.125rem 0;
  }

  &__quote {
    margin: 2rem 0;
    padding: 0 1rem;

    @include tablet {
      padding: 0 4rem;
    }
  }

  &__galery {
    position: relative;
    padding-top: 2rem;

    @include tablet {
      padding-top: 6.875rem;
    }
  }

  &__footer {
    display: inline-block;
    padding-top: 2.5rem;
    color: $middle-gray;
  }

  &__shape {
    position: absolute;
    top: -17rem;
    left: 1.4rem;
    z-index: -1;
    opacity: 0.5;
  }
}

