.blog-entry-list {
  padding-bottom: 3.75rem;

  @include tablet {
    padding-bottom: 5rem;
  }

  @include desktop {
    padding-bottom: 6.25rem;
  }
}

.blog {

		&__wrapper {
		}

		&__container {
		}

		&__list {
		}

		&__item {
      display: flex;
      flex-direction: column;

      &:not(:last-of-type) {
        margin-bottom: 1.5rem;
      }

      @include tablet {
        flex-direction: row;
      }
		}

		&__image {
      display: flex;

      img {
        height: 100%;
        object-fit: cover;
      }
		}

		&__body {
      display: flex;
      flex-direction: column;
      padding: 2.5rem 1.75rem;
      background-color: $light-gray;
		}

		&__date {
      margin-bottom: 0.625rem;
      color: $basic-green;
		}

		&__title {
      margin-bottom: 2.5rem;
		}

		&__info {
		}

		&__icon {
      margin-top: auto;
		}
}
