
.banner {
  position: relative;
  overflow: hidden;

  &__wrapper {
    position: relative;
  }

  &__container {
    display: flex;
    align-items: center;
    height: 21.875rem;

    @include desktop {
      height: 30rem;
    }
  }

  &__body {
    max-width: 50rem;
  }

  &__title {
    position: relative;
    margin-bottom: 2.5rem;
    padding-bottom: 1rem;
    font-size: clamp(2rem, 3vw, 3.0625rem);
    line-height: 1.2;
    font-weight: 500;
    color: #1D1D1D;

    &:after {
      @include pseudo-element;

      left: 0;
      bottom: 0;
      width: 4.375rem;
      height: 4px;
      background-color: $basic-green;
    }
  }

  &__info {
    font-size: clamp(1.25rem, 2vw, 1.375rem);
    line-height: 1.45;
  }

  &__shape {
    position: absolute;
    top: 1.875rem;
    right: -87%;

    z-index: -1;

    @include bgmobile {
      right: -40%;
    }

    @include tablet {
      top: 5.625rem;
      right: 7.5rem;
    }
  }

  &__nav {
    padding: 2.5rem 0;
    border-top: 1px solid $background-gray;
	}

	&__nav-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    @include tablet {
      flex-direction: row;
      flex-wrap: wrap;
    }
	}

	&__nav-item {
    @include tablet {
      padding: 0 2.5rem;

      &:first-child {
        padding-left: 0;
      }
    }
	}

	&__nav-link {
    position: relative;
    text-transform: uppercase;

    @include tablet {
      &:before {
        @include pseudo-element;
        top: -2rem;
        left: 0;
        width: 100%;
        height: 3px;
        opacity: 0;
        background: $basic-green;
        transition: all 0.3s ease-in-out;
      }

      &:hover {
        &:before {
          top: -2.5rem;
          opacity: 1;
        }
      }

      &--active {

        &:before {
          top: -2.5rem;
          opacity: 1;
        }
      }
    }

    &--active {
      color: $basic-green;
    }
	}

  &__footer {
    display: flex;
    margin-bottom: 2.5rem;
	}

	&__footer-item {
    padding-right: 5rem;
	}

	&__footer-title {
    padding-bottom: 0.625rem;
    color: $basic-green;
	}
}

.action-banner {
  padding: 2rem 0;
  background: $light-blue;

  @include tablet {
    padding: 0;
    background: linear-gradient(180deg, $white 0%, $white 55px, $light-blue 55px, $light-blue 100%);
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    max-width: 65.5rem;
    flex-wrap: wrap;

    @include tablet {
      flex-wrap: nowrap;
    }
  }

  &__info {
    max-width: 32.5rem;

    @include tablet {
      margin-top: 3.4375rem;
    }
  }

  &__title {
    font-size: clamp(2rem,3vw,3.0625rem);
    line-height: 1.2;
    font-weight: 700;
    color: $black;
  }

  &__links {
    display: flex;
    gap: 0.5rem;
    padding-top: 3.125rem;
  }

  &__image {
    display: none;

    @include tablet {
      display: flex;
    }
  }
}


