.footer {
  .footer-subscribe, .footer-contact, .footer-menu {
    grid-column: span 4;

    @include tablet {
      grid-column: span 12;
    }
    @include desktop {
      grid-column: span 4;
    }
  }
}

.footer-subscribe {

  &__title {
    margin-bottom: 0.5rem;
  }

  &__form {
    padding-top: 1rem;
  }

  &__social {
    padding-top: 1rem;

    .social-icon__link {
      &--facebook {
        .social-icon {
          width: 14px;
          height: 30px;
        }
      }
      &--twitter {
        .social-icon {
          width: 25px;
          height: 23px;
        }
      }
      &--instagram {
        .social-icon {
          width: 25px;
          height: 25px;
        }
      }
    }
  }

  &__nav-link {
    display: inline-block;
    padding: 0.4rem 0;
  }

  &__nav--bottom {
    padding-top: 2.25rem;
  }
}

.footer-contact {

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include desktop {
    align-items: center;
  }

  &__group {
    &:last-of-type {
      padding-top: 2.25rem;
    }
  }
}

.footer-menu {

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include desktop {
    align-items: center;
  }
}
