.blog {
  background-color: $light-blue;

  &__categs {
    margin-bottom: 2.5rem;
  }

  &__categs-list {
    display: flex;
    flex-direction: column;
    gap: 1.125rem;

    @include tablet {
      flex-direction: row;
    }
  }

  &__categs-item {
    padding-right: 2rem;
  }

  &__categs-item--active {
    .blog__categs-link {
      color: $basic-green;
    }
  }

  &__categs-link {
    letter-spacing: 1px;
    color: $black;
  }

  &--white-background {
    .blog-item__content {
      background-color: $light-gray;
    }
  }
}

.blog-item {

  display: flex;
  flex-direction: column;

  &:hover {
    .blog-item__image {
      transform: scale(1.025);
    }
  }

  &.anim-items {
    opacity: 0;
    transform: translateY(40px);
    transition: transform 1s cubic-bezier(.25,.46,.45,.94), opacity 0.8s cubic-bezier(.25,.46,.45,.94);
  }

  &.animate {
    $basic-delay: 0.32s;
    animation: none;

    @for $i from 1 through 12 {
      &:nth-of-type(#{$i}) {
        transition-delay: $basic-delay;
      }
      $basic-delay: $basic-delay + 0.16s;
    }

    opacity: 1;
    transform: translateY(0);
  }

  &__image-container {
    position: relative;
    overflow: hidden;
    padding: 0 0 65% 0;

    @include tablet {
      padding: 0 0 80% 0;
    }
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
    transition: transform 0.3s;
  }

  &__content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 1.875rem;
    background-color: $white;
  }

  &__subtitle {
    margin-bottom: 0.625rem;
  }

  &__description {
    margin-top: 1rem;
    color: $darkgrey;
  }

  &__tags {
    padding-top: 3rem;
    display: flex;
  }

  &__tag {
    font-size: 0.875rem;
    letter-spacing: 1px;
    line-height: 1;
    color: $darkgrey;
    margin-right: 0.625rem;
  }

  &__icon-container {
    padding-top: 1.5625rem;
    margin-top: auto;
  }
}
