.header {
  position: relative;
  top: 0;
  left:0;
  width: 100%;
  z-index: 6;

  background-color: $white;
  border-bottom: 1px solid $background-gray;

  &__wrapper {
    position: relative;
    margin: auto;
    padding-left: 20px;
    padding-right: 20px;

    @include tablet {
      padding-left: 30px;
      padding-right: 30px;
    }

    @include desktop {
      padding: 0 1rem;
      max-width: 1624px;
    }

    @include bgdesktop {
      max-width: 1680px;
    }
  }

  &__container {
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    img {
      width: 8.8125rem;
      height: 2.125rem;

      @include desktop {
        width: 14.375rem;
        height: 3.5rem;
      }
    }

    &:hover {
      opacity: 0.8;
    }
  }

  &__toggles {
    position: relative;
    overflow: hidden;
    width: 3.75rem;
    height: 3.75rem;

    @include desktop {
      width: 5.625rem;
      height: 5.625rem;
    }

    @include bgdesktop {
      width: 6.4375rem;
      height: 6.4375rem;
    }
  }

  &__toggle {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: absolute;
    top: 0;
    left: 0;

    width: 3.75rem;
    height: 3.75rem;

    background-color: $white;

    cursor: pointer;
    transition: all .5s ease;


    @include desktop {
      width: 5.625rem;
      height: 5.625rem;
    }

    @include bgdesktop {
      width: 6.4375rem;
      height: 6.4375rem;
    }

    &--close {
      opacity: 0;
      display: none;
      transform: scale(0);
      transition: transform 0.7s ease-in-out, opacity 0.4s ease-in-out;
      transition-delay: 0.2s;
    }
  }

  &__toggle-icon {
    path {
      fill: $basic-green;
      stroke: $basic-green;
    }
  }
}

.header--index {
  border-bottom: 0;
  background-color: transparent;

  @include tablet {
    position: absolute;
  }

  .main-nav .main-nav__link {
    color: $white;
  }

  .header__toggle {
    align-items: center;
    justify-content: center;

    background-color: $basic-green;

    &:hover {
      background-color: $hover-green;
    }
  }

  .header__toggle-icon {
    path {
      fill: $white;
      stroke: $white;
    }
  }

  .main-nav__list {
    padding-right: 6.25rem;
  }
}
